<template>
    <div class="price">
  		<dl>
            <dt>全部价格</dt>
  			<dd v-for="(item,index) in priceList" @click="priceClickEvent(index)"> {{item.prices}} </dd>
  		</dl>
  		<p class="price_tittle">自定义价格 <span>(万元)</span> </p>
  		<div class="price_search">
  			<input id="minprice" type="number" oninput="if(value.length>4)value=value.slice(0,4)"  v-model="lowprice">
  			<span class="line">-</span>
  			<input type="number" oninput="if(value.length>4)value=value.slice(0,4)" v-model="highprice">
  			<span class="custom_btn" @click="custom">确定</span>
  		</div>

        <div style="width:90%;margin:0 auto;">
            <input type="hidden" class="slider-price" value="123" />
        </div>

        <div class="searchPrice" @click="custom">确定</div>

    </div>
</template>

<script>

export default {
  data () {
    return {
      priceList: [
        {prices:'5万以下'},
        {prices:'5-10万'},
        {prices:'10-15万'},
        {prices:'15-20万'},
        {prices:'20-30万'},
        {prices:'30-50万'},
        {prices:'50万以上'}
      ],
      lowprice:'',
      highprice:'',
      priceBooleans:false,
    }
  },
  created(){
      var _self = this;
      setTimeout(function(){
          $('.slider-price').jRange({
    	    from: 0,
    	    to: 55,
    	    step: 5,
            snap:true,
    	    showLabels: true,
    	    isRange : true,
            ondragend:ondragend
    	},0);

        var low = $('.low').eq(1).html(),
            heigh = $('.high').eq(1).html();

        if(heigh == '55'){
            $('.high').eq(1).html('不限');
        }

        function ondragend(){
            var low = $('.low').eq(1).html(),
                heigh = $('.high').eq(1).html(),
                searchPrice = $('.searchPrice');

            if(low !== '0' || heigh !== '55'){
                searchPrice.addClass('priceActive');
                _self.priceBooleans = true;
            }else if(low == '0' && heigh == '55'){
                searchPrice.removeClass('priceActive');
                _self.priceBooleans = false;
            }

            if(heigh == '55'){
                $('.high').eq(1).html('不限');
            }

            if(low == '0' && heigh == '不限'){
                searchPrice.removeClass('priceActive');
                _self.priceBooleans = false;
            }

            _self.lowprice = low;
            _self.highprice = heigh;
        }
    },10)

  },
  methods:{
    priceClickEvent(index){
      var motorcycleType = this.priceList[index].prices;
      var False = false;
      this.$emit('priceClickEvent',motorcycleType,False);
    },
    hidePrice(){
      var False = false;
      this.$emit('hidePrice',False);
    },
    custom(){
        var False = false;
        if(this.priceBooleans == true){
            let min_price = "";
            let max_price = "";
            let custom = {};
            
            if (this.lowprice == '0' && this.highprice !== '不限') {
                var motorcycleType = this.highprice + '万以下';
                min_price = this.lowprice;
                max_price = this.highprice;
            } else if (this.lowprice != '0' && this.highprice == '不限') {
                var motorcycleType = this.lowprice + '万以上';
                min_price = this.lowprice;
                max_price = "";
            } else if (this.lowprice != '0.00' && this.highprice != '不限') {
                var motorcycleType = this.lowprice + '-' + this.highprice + '万';
                min_price = this.lowprice;
                max_price = this.highprice;
            }
            custom.priceRange = [min_price,max_price].join(',');
            this.$emit('priceClickEvent',motorcycleType,custom,False);
        }
    }
  }
}
</script>





<style lang="scss">
.price{
  position: absolute;
  left:0;
  top:.7rem;
  overflow: hidden;
  width: 100%;
  background: white;
  z-index: 2;
  padding-bottom: 2rem;
	dl{
		width: 100%;
        dt{
            text-indent: 1em;
            font-size: .28rem;
            height: .86rem;
            line-height: .86rem;
            border-top: 1px solid #f0f0f0;
        }
		dd{
			height:.8rem;
			line-height: .8rem;
			color: #222;
			padding-left: .24rem;
			font-size: .28rem;
			border-top: 1px solid #f0f0f0;
		}
	}
	.price_tittle{
		font-size: .28rem;
		color: #545454;
		padding-left: .24rem;
        padding-top: .3rem;
		margin-bottom: .78rem;
        border-top: .3rem solid #f6f6f6;
        span{
            color: #666;
        }
	}
	.price_search{
		display: flex;
        display: none;
		input{
			width: 2rem;
			height:.7rem;
			line-height: normal;
			border:1px solid #cdcdcd;
			text-indent: 1em;
			border-radius: 10px;
			font-size: .26rem;
      text-align: center;
		}
		input:first-child{
			margin-left: .35rem;
		}
		.line{
			font-size: .5rem;
			margin:0 .1rem;
			color: #646464;
      height: .7rem;
      line-height: .7rem;
		}
		.custom_btn{
			width: 2rem;
			height: .7rem;
			background: #ffa31c;
			text-align: center;
			line-height: .7rem;
			border-radius: 10px;
			font-size: .26rem;
			margin-left: .2rem;
      color: #fff;
		}
	}
}
.slider-container{
    margin-left: .4rem;
}
.scale{
    display: none;
}
.searchPrice{
    width: 5.74rem;
    height: .7rem;
    background: #adadad;
    color: #fff;
    text-align: center;
    line-height: .7rem;
    margin:1rem auto 0;
    border-radius: 20px;
    box-shadow: 0 0 10px #adadad;
    font-size: .3rem;
}
.searchPrice.priceActive{
    background: #ff9600;
    box-shadow: 0 0 10px #ff9600;
}
</style>



